.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container {
  margin: auto;
}

.logo {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 40px !important;
  width: 40% !important;
}

.topic {
  text-align: center;
  background-color: #042c5c;
  color: #ffffff;
  font-family: "Arita Sans" !important;
  padding-top: 16px !important;
  padding-bottom: 16px !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.imageInput {
  border-bottom: 2px solid black;
  border-right: 2px solid black;
  border-top: 2px solid black;
  border-left: 2px solid black;
  padding: 13px;
  margin: 15px;
  cursor: pointer;
  float : right ;
}

.imgPreview {
  text-align: center;
  margin: 5px 15px !important;
  height: 250px !important;
  width: 250px !important;
  border-left: 4px solid rgb(21, 61, 2) !important;
  border-right: 4px solid rgb(21, 61, 2) !important;
  border-top: 4px solid rgb(21, 61, 2) !important;
  border-bottom: 4px solid rgb(21, 61, 2) !important;
  float : left;  
}

#input_img, #no_img {
  text-align: center;
  height: 256px !important;
  width: 256px !important;
  box-sizing: content-box !important;
  border-left: 1px solid gray;
  border-right: 1px solid gray;
  border-top: 1px solid gray;
  border-bottom: 1px solid gray;
}

